import { defineComponent, ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import ModalComponent from '../components/Modal.vue';
import { NDropdown, NIcon, useDialog, useMessage } from 'naive-ui';
import { AddSharp } from '@vicons/ionicons5';
import { DEFAULT_COLOR } from '@/constant';
import { transitionTime } from '@/utils';
export default defineComponent({
    name: 'PageTheme',
    components: {
        NDropdown,
        NIcon,
        AddSharp,
        ModalComponent
    },
    setup() {
        const dialog = useDialog();
        const message = useMessage();
        const $store = useStore();
        const $router = useRouter();
        const themeList = computed(() => $store.state.themeUserConfig);
        const showModal = ref(false);
        const options = [
            {
                label: '',
                key: 'see'
            },
            {
                label: '',
                key: 'edit'
            },
            {
                label: '',
                key: 'copy'
            },
            {
                label: '',
                key: 'delete'
            },
            {
                label: '',
                key: 'download'
            }
        ];
        let optionType = '';
        let currentTheme = {};
        const useModalOpen = (type) => {
            optionType = type;
            showModal.value = true;
        };
        const useModalClose = () => {
            showModal.value = false;
        };
        const handleSelect = (key, theme) => {
            optionType = key;
            currentTheme = theme;
            switch (key) {
                case 'see':
                    $store.dispatch('useTheme', theme);
                    $router.push('/preview');
                    break;
                case 'edit':
                    useModalOpen(key);
                    break;
                case 'copy':
                    useModalOpen(key);
                    break;
                case 'delete':
                    dialog.error({
                        title: '',
                        content: 'o()o',
                        positiveText: '',
                        negativeText: '',
                        maskClosable: false,
                        onPositiveClick: () => {
                            $store.dispatch('deleteTheme', currentTheme.id);
                            message.success('emmm');
                        }
                    });
                    break;
                case 'download':
                    $store.dispatch('downloadTheme', currentTheme);
            }
        };
        const useModalConfirm = ({ nameValue: name }) => {
            const type = optionType;
            switch (type) {
                case 'add':
                    $store.dispatch('addTheme', { name }).then(theme => {
                        $store.dispatch('useTheme', theme);
                        $router.push('/preview');
                    });
                    break;
                case 'edit':
                    $store.dispatch('updateTheme', {
                        id: currentTheme.id,
                        name,
                        theme: currentTheme.theme,
                        update: currentTheme.update
                    });
                    break;
                case 'copy':
                    $store.dispatch('addTheme', {
                        name,
                        theme: currentTheme.theme
                    });
                    break;
            }
        };
        return {
            defaultColor: DEFAULT_COLOR,
            transitionTime,
            themeList,
            options,
            showModal,
            useModalOpen,
            useModalClose,
            useModalConfirm,
            handleSelect
        };
    }
});
