import { defineComponent, ref, reactive, watchEffect } from 'vue';
import { NModal, NSpace, NForm, NFormItem, NInput, NButton } from 'naive-ui';
export default defineComponent({
    name: 'ModalComponent',
    components: {
        NModal,
        NSpace,
        NForm,
        NFormItem,
        NInput,
        NButton
    },
    emits: ['close', 'confirm'],
    props: {
        show: {
            type: Boolean,
            default: false
        }
    },
    setup(props, { emit }) {
        const formRef = ref();
        const modalShow = ref(props.show);
        const modalTitle = '~';
        const modalStyle = {
            width: '600px'
        };
        const formModel = reactive({
            nameValue: ''
        });
        const formRules = {
            nameValue: {
                required: true,
                message: '~',
                trigger: ['input']
            }
        };
        const modalClose = () => {
            emit('close');
        };
        async function modalValidate() {
            await formRef.value.validate();
            emit('confirm', formModel);
            modalClose();
        }
        watchEffect(() => {
            modalShow.value = props.show;
            props.show && (formModel.nameValue = '');
        });
        return {
            modalShow,
            modalTitle,
            modalStyle,
            formRef,
            formModel,
            formRules,
            modalClose,
            modalValidate
        };
    }
});
